<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="mb-base" v-if="contactMechList.length === 0">
        <div class="alert-container">
          <vs-alert active="true" color="warning">
            <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
          </vs-alert>
        </div>
      </div>
      <div class="vs-row flex mb-base" v-if="contactMechList.length > 0">
        <div class="nv-col sm:w-1/2 w-full" v-for="(category, index) in contactCategories" :key="index">
          <h4 class="mb-3">{{ $t(index) }}</h4>
          <ul>
            <li class="mb-5" v-for="(contactMech, catIndex) in category" :key="catIndex">
              <vs-checkbox v-model="contactMechs" :vs-value="contactMech.contactMechId" >
                {{ contactMechDetails(contactMech) }} <span class="italic text-sm text-primary">({{ contactMech.contactMechPurposeDescription }})</span>
              </vs-checkbox>
            </li>
          </ul>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <vs-button class="btnx" type="filled" @click="resetPasswordUser()">{{ $t('SUBMIT') }}</vs-button>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import PartyService from '@/services/nucleus/PartyService'

export default {
  name: 'user-reset-password',
  mixins: [AlertMixin],
  data: function () {
    return {
      user: {},
      contactMechList: [],
      contactMechs: []
    }
  },
  computed: {
    contactCategories () {
      let categories = {}
      for (let i = 0; i < this.contactMechList.length; i++) {
        let key = this.contactMechList[i].contactMechTypeEnumId === 'CmtEmailAddress' ? 'EMAILS' : 'PHONE_NUMBERS'
        if (categories.hasOwnProperty(key)) {
          categories[key].push(this.contactMechList[i])
        } else {
          categories[key] = [this.contactMechList[i]]
        }
      }
      return categories
    }
  },
  methods: {
    async load () {
      let self = this
      SecurityService.users({
        userId: this.$route.params.id
      }).then(function (response) {
        self.user = response.data.userList[0] || {}

        PartyService.partyContactMechs({
          partyId: self.user.partyId
        }).then(function (response) {
          self.contactMechList = response.data.partyContactMechList || []
        })
      })
    },
    contactMechDetails (item) {
      if (item.contactMechTypeEnumId === 'CmtTelecomNumber') {
        let phoneNumber = '+'
        phoneNumber += item.countryCode ? item.countryCode : ''
        phoneNumber += item.areaCode ? '-' + item.areaCode : ''
        phoneNumber += item.contactNumber ? '-' + item.contactNumber : ''
        phoneNumber += item.extension ? 'x' + item.extension : ''
        return phoneNumber
      } else {
        return item.infoString
      }
    },
    resetPasswordUser (nextAction) {
      let self = this
      SecurityService.resetInternalUserPassword({
        userId: this.user.userId,
        contactMechs: this.contactMechs.join(',')
      }).then(function (response) {
        if (response.status === 200) {
          self.showSuccessAlert(response.data.messages)
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
